body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background:hsl(210, 36%, 96%);
  color:hsl(209, 61%, 16%) ;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1,
h2,
h3,
h4 {
  letter-spacing: 0.1rem;
  text-transform: capitalize;
  line-height: 1.25;
  margin-bottom: 0.75rem;
}

.section {
  width: 90vw;
  margin: 0 auto;
  max-width: 1170px;
}

main {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.quiz {
   width: 90vw;
   max-width: 1170px;
   margin:  4rem auto;
   background:  #fff;
   border-radius:  0.25;
   padding: 3rem;
 }

 .quiz-small {
  max-width: 500px;
 }

 .next-question,
 .start-btn,
 .answer-btn,
 .close-btn {
  padding: 0.25rem 0.5rem;
  display: 100%;
  width: 100%;
  margin-left: auto;
  margin-top: 2rem;
  margin-right: auto;
 }

 .error{
  color: darkred;
  text-transform: capitalize;
 }

 .spinner {
  margin-top:  300px;
 }

 .spinner-border {
  width: 6rem;
  height: 6rem;
 }

 .modal-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.75);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: all 0.3s linear;
  z-index: -1;
 }

 .isOpen {
   opacity: 1;
   z-index: 999;
 }

 .modal-content {
  background: #fff;
  width: 90vw;
  max-width: 670px;
  padding: 3rem;
  border-radius: 0.25rem;
  text-align: center;
  position:relative;
 }

 .model-content p {
  font-size: 1.5rem;
  text-transform: none;
 }

